<template>
  <div class="findbutton">
    <div class="findbutton__wrapper col-lg-8 col-lg-offset-2 col-xs-12">
      <div class="findbutton__img">
        <img src="../../assets/house.png" alt="">
      </div>
      <div class="findbutton__title"></div>
      <div class="findbutton__subtitle"><br>
  </div>
      <div class="findbutton__button" @click="toMore()">
        <img src="../../assets/search.png">
      </div>
    </div>
  </div>
</template>
<style lang="scss">
  @import "../../assets/scss/defined.scss";
  @import "../../assets/scss/pages/findbutton.scss";
</style>
<script>
export default {
  name:'findbutton',
  methods:{
    toMore(){
      this.$router.push({path:'/find'})
    }
  }
}
</script>



