<template>
  <div class="contact">
    <subBanner :useWhichBanner="whichBanner"></subBanner>
    <div class="col-lg-8 col-lg-offset-2 col-xs-12 contactWrapper">
      <div class="contactUs">
        <div class="contactTitle"></div>
        <div class="contactWords">
          <div class="contactSubTitle"></div>
          <div class="contactText">
            <p v-for="item in contactWay" :key="item.title">{{item.title}}:{{item.content}}</p>
          </div>
        </div>
        <div class="contactLogo">
          <img src="./../../assets/logoFont.png" alt="">
        </div>
        <div class="contactMap">
          <div id="mymap"></div>    
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
@import "../../assets/scss/defined.scss";
@import "../../assets/scss/pages/contact.scss";
.anchorBL {
  display: none;
}
</style>
<script>
import subBanner from "./../subBanner";
export default {
  data() {
    return {
      whichBanner: "contactBanner",
      contactWay: [
        {
          title: "",
          content: "123456"
        },
        {
          title: "",
          content: "4008123123"
        },
        {
          title: "",
          content: "13123456789"
        },
        {
          title: "QQ",
          content: "123456789"
        },
        {
          title: "",
          content: "22601"
        }
      ]
    };
  },
  methods: {
    baiduMap() {
      // var trash = document.querySelectorAll('#mymap .anchorBL');
      // console.log(trash);

      // API
      var map = new BMap.Map("mymap"); // Map
      map.centerAndZoom(new BMap.Point(121.48848, 29.890014), 16); // ,
      //
      map.addControl(
        new BMap.MapTypeControl({
          mapTypes: [BMAP_NORMAL_MAP, BMAP_HYBRID_MAP]
        })
      );
      map.setCurrentCity(""); //  
      map.enableScrollWheelZoom(true); //
      var point = new BMap.Marker(new BMap.Point(121.48848, 29.890014)); // 
      map.addOverlay(point);
      var local = new BMap.LocalSearch(map, {
        renderOptions: { map: map }
      });
    }
  },
  components: {
    subBanner
  },
  mounted() {
    this.baiduMap();
  }
};
</script>



